html, body {
  width: 100%;
  height: 100%;
  background: rgba(236, 241, 245, 1);
}
#wrap {
  display: -moz-flex;
  display: -ms-flex;
  display: flex;
  max-width: 1224px; 
  margin: 0 auto 0;
  background: rgba(255, 255, 255, 1);
}
.container{
  position: relative;
  display: -moz-flex;
  display: -ms-flex;
  display: flex;
  min-height: 100vh;
  background-color: #fff;
  box-shadow: 0 0 11px rgba(0,0,0,0.2);
  -moz-flex: 1;
  flex: 1 1;
  -moz-flex-direction: column;
  flex-direction: column;
}
.top{
  position: relative;
  display: -moz-flex;
  display: -ms-flex;
  display: flex;
  -moz-align-items: center;
  -ms-align-items: center;
  align-items: center;
  height: 58px;
  justify-content: space-between;
}
.top .logo {
  display: -moz-flex;
  display: -ms-flex;
  display: flex;
  -moz-align-items: center;
  -ms-align-items: center;
  align-items: center; 
  height: 58px;
  margin:0 0 0 32px;  
}
.top .logo .home-logo{
  position: relative;
  display: block;  
  width: 194px;
  height: 38px; 
  background-image: url("https://dev.rankingball.com:13001/img/Logo%20Copy@2x.png");
  background-size: 100% 100%;
  background-position: 0 0;
  background-repeat: no-repeat;
}
.top .logo .sub-logo {
  width: 106px;
  height: 17px;
  margin: 0 0 0 5px;
  background-image: url("https://dev.rankingball.com:13001/img/for-raffle@2x.png");
  background-size: 100% 100%;
  background-position: 0 0;
  background-repeat: no-repeat;
}
.top .logo .goto-gdc {
  position: relative;
  width: 172px;
  height: 40px;
  margin: 0 0 0 22px;
  background-image: url("https://dev.rankingball.com:13001/img/btn-arena-cast2@2x.png");
  background-size: 100% 100%;
  background-position: 0 0;
  background-repeat: no-repeat;
  cursor: pointer;
}
.top .infor-coin{
  display: -moz-flex;
  display: -ms-flex;
  display: flex;	
}
.top .infor-player{
  display: -moz-flex;
  display: -ms-flex;
  display: flex;	
	padding: 0 0 0 20px;	
	text-align: center;
}
.top .infor-player span{
  display: -moz-flex;
  display: -ms-flex;
  display: flex;	
	width:30px;
	height:30px;
	margin: 0 18px 0 0;
	background-position:0 50%;
	background-repeat:no-repeat;		
	justify-content: center;
  -moz-flex-direction: column;
  flex-direction: column;
  text-align: center;
  cursor:pointer;
}
.top .infor-player .noti {
  position: relative;  
  background-image: url("https://dev.rankingball.com:13001/img/Notice@2x.png");
  background-size: 30px 30px;
  background-repeat: no-repeat;
}
.top .infor-player .guide {
  position: relative;  
  background-image: url("https://dev.rankingball.com:13001/img/Help@2x.png");
  background-size: 30px 30px;
  background-repeat: no-repeat;
}
.top .infor-player .inbox {
  position: relative;  
  background-image: url("https://dev.rankingball.com:13001/img/InBox@2x.png");
  background-size: 30px 30px;
  background-repeat: no-repeat;
}
.top .infor-player .my-infor {
  position: relative;  
  background-image: url("https://dev.rankingball.com:13001/img/User@2x.png");
  background-size: 30px 30px;
  background-repeat: no-repeat;
}
.content{
  position: relative;
  display: -moz-flex;
  display: -ms-flex;
  display: flex;
  -moz-flex: 1;
  flex: 1 1;
  -moz-flex-direction: column;
  flex-direction: column;
}
.container > footer {
  position: relative; 
  padding: 25px 32px 0;
  font-size: 10px;
  color: #ccc;
  background: rgba(92,95,110,1);
  overflow: hidden;
}
.container > footer p{
	margin:0 0 12px 0;
}
.container > footer .footer-txt{
	float:left;
}
.container > footer .footer-txt a{
	color:#ccc;
}
.container > footer .footer-link{
	float:left;
	padding: 7px 0 0 53px;
}
.container > footer .footer-link a{
	display:inline-block;
}
